.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 0;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 6px;
  background: linear-gradient(135deg, #ff0000, #ff4444);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
  border-radius: 3px;
}

.timeline-item {
  padding: 10px 40px;
  position: relative;
  width: 50%;
  animation: fadeInUp 0.6s ease forwards;
}

.timeline-item.left {
  left: 0;
}

.timeline-item.right {
  left: 50%;
}

.timeline-item::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px;
  background: linear-gradient(135deg, #ff0000, #ff4444);
  border: 4px solid white;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

.timeline-item.right::after {
  left: -17px;
}

.timeline-content {
  padding: 20px 30px;
  background: #1a1a1a;
  position: relative;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
}

.timeline-header {
  margin-bottom: 1rem;
}

.job-title {
  color: white;
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.company-name {
  color: #ff0000;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.job-meta {
  display: flex;
  gap: 1rem;
  font-size: 0.9rem;
  color: #e2e8f0;
}

.period {
  font-weight: 500;
}

.location {
  font-style: italic;
}

.job-description {
  color: #e2e8f0;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.technologies {
  margin-bottom: 1.5rem;
}

.technologies h5 {
  color: white;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: linear-gradient(135deg, #ff0000, #ff4444);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
}

.achievements h5 {
  color: white;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.achievements ul {
  list-style: none;
  padding: 0;
}

.achievements li {
  color: #e2e8f0;
  padding: 0.25rem 0;
  position: relative;
  padding-left: 1.5rem;
}

.achievements li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: #ff0000;
  font-weight: bold;
}

.experience-summary {
  margin-top: 4rem;
}

.summary-card {
  background: #1a1a1a;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}

.summary-card h3 {
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 2rem;
  text-align: center;
}

.highlights-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
}

.highlight-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 10px;
  background: #333333;
  transition: all 0.3s ease;
}

.highlight-item:hover {
  background: #444444;
  transform: translateY(-2px);
}

.highlight-item i {
  font-size: 2rem;
  color: #ff0000;
}

.highlight-item h4 {
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.highlight-item p {
  color: #e2e8f0;
  font-size: 0.9rem;
  margin: 0;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .timeline::after {
    left: 31px;
  }

  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  .timeline-item.left,
  .timeline-item.right {
    left: 0;
  }

  .timeline-item::after {
    left: 15px;
    right: auto;
  }

  .highlights-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .timeline-item {
    padding-left: 60px;
    padding-right: 15px;
  }

  .timeline-content {
    padding: 15px 20px;
  }

  .highlights-grid {
    grid-template-columns: 1fr;
  }

  .job-meta {
    flex-direction: column;
    gap: 0.25rem;
  }

  .tech-tags {
    gap: 0.25rem;
  }

  .tech-tag {
    font-size: 0.75rem;
    padding: 0.2rem 0.5rem;
  }
} 