:root {
  --bg: #0b0b0b;
  --card: #131313;
  --text: #eaeaea;
  --muted: #b3b3b3;
  --accent: #ff2a2a;
}

.linkhub {
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: radial-gradient(1000px 600px at 50% -10%, rgba(255,42,42,0.15), transparent 60%),
              radial-gradient(600px 600px at 90% 10%, rgba(255,42,42,0.08), transparent 60%),
              linear-gradient(135deg, #000000 0%, #1a1a1a 100%);
  padding: 2rem;
}

.card {
  width: 100%;
  max-width: 560px;
  background: var(--card);
  border: 1px solid #2a2a2a;
  border-radius: 20px;
  padding: 2rem;
  color: var(--text);
  box-shadow: 0 10px 40px rgba(0,0,0,0.4);
}

.avatar {
  position: relative;
  display: grid;
  place-items: center;
  margin-bottom: 0.75rem;
}

.avatar img {
  width: 110px;
  height: 110px;
  border-radius: 999px;
  object-fit: cover;
  border: 3px solid var(--accent);
}

/* Logo Section with Smoke Video Background */
.logo-section {
  position: relative;
  display: grid;
  place-items: center;
  margin-bottom: 1.5rem;
  height: 120px;
  border-radius: 16px;
  overflow: hidden;
}

.logo-bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px;
}

.logo-overlay {
  position: absolute;
  inset: 0;
  background: radial-gradient(200px 80px at 50% 50%, rgba(0,0,0,0.1) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0.6) 100%);
  pointer-events: none;
}

.main-logo {
  position: relative;
  z-index: 1;
  height: 80px;
  width: auto;
  filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.4));
  transition: transform 0.3s ease, filter 0.3s ease;
}

.main-logo:hover {
  transform: scale(1.05);
  filter: drop-shadow(0 0 30px rgba(255, 255, 255, 0.6));
}

.title-wrap {
  position: relative;
  display: grid;
  place-items: center;
  margin-bottom: 1rem;
}

.title-bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px;
}

.title-overlay {
  position: absolute;
  inset: 0;
  background: radial-gradient(200px 60px at 50% 50%, rgba(0,0,0,0) 0%, rgba(0,0,0,0.25) 70%, rgba(0,0,0,0.45) 100%);
  pointer-events: none;
}

.title-wrap h1 {
  position: relative;
  margin: 0;
  padding: 0.5rem 0.75rem;
  z-index: 1;
}

h1 {
  text-align: center;
  font-size: 1.6rem;
  margin: 0;
}

.tagline {
  text-align: center;
  color: var(--muted);
  margin: 0.5rem 0 1.5rem;
}

.links {
  display: grid;
  gap: 0.9rem;
}

.link {
  display: block;
  text-align: center;
  padding: 0.95rem 1.25rem;
  border-radius: 12px;
  text-decoration: none;
  color: var(--text);
  background: #1b1b1b;
  border: 1px solid #2a2a2a;
  transition: transform .15s ease, background .2s ease, border-color .2s ease;
}

.link .link-content {
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
}

.link.has-image .link-content {
  gap: 0.8rem;
}

.thumb {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  object-fit: cover;
  flex: 0 0 40px;
  border: 1px solid #2a2a2a;
}

.link:hover {
  transform: translateY(-2px);
  background: #222222;
  border-color: var(--accent);
}

.socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1.25rem;
}

.socials a {
  color: var(--muted);
  font-size: 1.25rem;
}

@media (max-width: 480px) {
  .card { padding: 1.5rem; }
}

.media {
  margin-top: 1.25rem;
}

.video-embed {
  position: relative;
  width: 100%;
  aspect-ratio: 16 / 9;
  background: #000;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid #2a2a2a;
}

.video-embed iframe {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}


