:root {
  --epk-bg: #0b0b0b;
  --epk-card: #141414;
  --epk-border: #2a2a2a;
  --epk-text: #eaeaea;
  --epk-muted: #b3b3b3;
  --epk-accent: #ff2a2a;
}

.epk {
  min-height: 100vh;
  background: linear-gradient(135deg, #000000 0%, #1a1a1a 100%);
  color: var(--epk-text);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.epk-container {
  width: 100%;
  max-width: 920px;
  background: var(--epk-card);
  border: 1px solid var(--epk-border);
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.4);
}

.epk-header {
  padding: 1.25rem 1.5rem;
  border-bottom: 1px solid var(--epk-border);
}

.epk-title {
  margin: 0;
  font-size: 1.8rem;
}

.epk-subtitle {
  margin: 0.35rem 0 0;
  color: var(--epk-muted);
}

.epk-hero {
  position: relative;
  height: 260px;
  background: #101010;
}

.epk-hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(0.75);
}

.epk-hero-overlay {
  position: absolute;
  inset: 0;
  background: radial-gradient(800px 300px at 20% -10%, rgba(255,42,42,0.2), transparent 60%);
}

.epk-content {
  padding: 1.5rem;
  display: grid;
  gap: 1rem;
  line-height: 1.8;
}

.epk-content h2 {
  margin-top: 0.5rem;
  margin-bottom: 0.25rem;
  font-size: 1.3rem;
}

.epk-list {
  margin: 0.5rem 0 0;
  padding-left: 1.25rem;
}

.epk-links {
  margin: 0.25rem 0 0.5rem;
  padding-left: 1.25rem;
}

.epk-links a {
  color: var(--epk-text);
  text-decoration: none;
  border-bottom: 1px dashed var(--epk-border);
}

.epk-links a:hover {
  border-color: var(--epk-accent);
}

.epk-footer {
  padding: 1rem 1.5rem 1.5rem;
}

.epk-cta {
  color: var(--epk-text);
  text-decoration: none;
  border: 1px solid var(--epk-border);
  padding: 0.6rem 0.9rem;
  border-radius: 10px;
}

.epk-cta:hover {
  border-color: var(--epk-accent);
}

@media (max-width: 640px) {
  .epk-hero { height: 200px; }
}


