.floating-social {
  position: fixed;
  top: 50%;
  right: 2rem;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 1.5rem;
  z-index: 1000;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
  min-width: 200px;
}

.floating-social:hover {
  transform: translateY(-50%) scale(1.02);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 255, 255, 0.2);
}

.social-header h4 {
  color: white;
  margin: 0 0 1rem 0;
  font-size: 1.1rem;
  text-align: center;
  font-weight: 600;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.social-links {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem;
  border-radius: 12px;
  text-decoration: none;
  color: white;
  transition: all 0.3s ease;
  border: 1px solid transparent;
}

.social-link:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateX(-5px);
}

.social-link i {
  font-size: 1.2rem;
  width: 20px;
  text-align: center;
}

.social-link span {
  font-size: 0.9rem;
  font-weight: 500;
}

/* Social link specific colors */
.social-link.youtube i {
  color: #ff0000;
}

.social-link.instagram i {
  color: #e4405f;
}

.social-link.artstation i {
  color: #00ff88;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .floating-social {
    right: 1rem;
    left: 1rem;
    transform: none;
    top: auto;
    bottom: 2rem;
    min-width: auto;
    width: calc(100% - 2rem);
  }
  
  .floating-social:hover {
    transform: none;
  }
  
  .social-links {
    flex-direction: row;
    justify-content: center;
    gap: 1rem;
  }
  
  .social-link {
    flex-direction: column;
    text-align: center;
    padding: 0.5rem;
    min-width: 80px;
  }
  
  .social-link span {
    font-size: 0.8rem;
  }
  
  .social-link:hover {
    transform: translateY(-3px);
  }
}

@media (max-width: 480px) {
  .floating-social {
    bottom: 1rem;
    right: 0.5rem;
    left: 0.5rem;
    width: calc(100% - 1rem);
    padding: 1rem;
  }
  
  .social-links {
    gap: 0.5rem;
  }
  
  .social-link {
    min-width: 70px;
    padding: 0.4rem;
  }
  
  .social-link span {
    font-size: 0.75rem;
  }
}
