.contact-intro {
  text-align: center;
  max-width: 600px;
  margin: 0 auto 3rem;
}

.contact-intro p {
  color: #e2e8f0;
  font-size: 1.1rem;
  line-height: 1.8;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  margin-top: 2rem;
}

.contact-info {
  background: #1a1a1a;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  grid-column: 1 / -1;
}

.contact-info h3 {
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.contact-info > p {
  color: #e2e8f0;
  line-height: 1.6;
  margin-bottom: 2rem;
}

.contact-details {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: #333333;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.contact-item:hover {
  background: #444444;
  transform: translateY(-2px);
}

.contact-icon {
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #000000, #333333);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
}

.contact-text h4 {
  color: white;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.contact-text a {
  color: #ff0000;
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.3s ease;
}

.contact-text a:hover {
  color: #ff4444;
}

.social-section h4 {
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.social-links {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  background: #333333;
  border-radius: 25px;
  color: var(--social-color);
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
}

.social-link:hover {
  background: var(--social-color);
  color: white;
  transform: translateY(-2px);
}

.contact-form-container {
  background: #1a1a1a;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}

.contact-form-container h3 {
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 2rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  color: white;
  font-weight: 600;
  font-size: 0.9rem;
}

.form-group input,
.form-group textarea {
  padding: 1rem;
  border: 2px solid #333333;
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #1a1a1a;
  color: white;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #ff0000;
  box-shadow: 0 0 0 3px rgba(255, 0, 0, 0.1);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-btn {
  background: linear-gradient(135deg, #ff0000, #ff4444);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(255, 0, 0, 0.3);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.submit-btn.submitting {
  background: #718096;
}

.success-message,
.error-message {
  padding: 1rem;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  animation: slideIn 0.3s ease;
}

.success-message {
  background: #c6f6d5;
  color: #22543d;
  border: 1px solid #9ae6b4;
}

.error-message {
  background: #fed7d7;
  color: #742a2a;
  border: 1px solid #feb2b2;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .social-links {
    justify-content: center;
  }

  .contact-form-container,
  .contact-info {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .contact-item {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }

  .contact-icon {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }

  .social-links {
    flex-direction: column;
    align-items: center;
  }

  .social-link {
    width: 200px;
    justify-content: center;
  }

  .form-group input,
  .form-group textarea {
    padding: 0.75rem;
  }

  .submit-btn {
    padding: 0.75rem 1.5rem;
  }
} 