.skills-intro {
  text-align: center;
  max-width: 600px;
  margin: 0 auto 3rem;
}

.skills-intro p {
  color: #4a5568;
  font-size: 1.1rem;
  line-height: 1.8;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
  margin-bottom: 4rem;
}

.skill-category {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.skill-category:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
}

.category-title {
  color: #2d3748;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  text-align: center;
  position: relative;
}

.category-title::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 3px;
  background: linear-gradient(90deg, #000000, #333333);
  border-radius: 2px;
}

.skills-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.skill-item {
  animation: fadeInUp 0.6s ease forwards;
}

.skill-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.skill-name {
  color: #2d3748;
  font-weight: 600;
  font-size: 1rem;
}

.skill-level {
  color: #ff0000;
  font-weight: 600;
  font-size: 0.9rem;
}

.skill-bar {
  width: 100%;
  height: 8px;
  background: #e2e8f0;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(90deg, #000000, #333333);
  border-radius: 4px;
  transition: width 1.5s ease;
  animation: slideIn 1.5s ease forwards;
}

@keyframes slideIn {
  from {
    width: 0;
  }
  to {
    width: var(--width);
  }
}

.skills-summary {
  margin-bottom: 4rem;
}

.summary-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
}

.summary-item {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.summary-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
}

.summary-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #000000, #333333);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1rem;
  font-size: 1.5rem;
  color: white;
}

.summary-item h4 {
  color: #2d3748;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.summary-item p {
  color: #4a5568;
  font-size: 0.9rem;
  line-height: 1.6;
}

.certifications {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}

.certifications h3 {
  color: #2d3748;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 2rem;
  text-align: center;
}

.cert-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.cert-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background: #f7fafc;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.cert-item:hover {
  background: #edf2f7;
  transform: translateY(-2px);
}

.cert-item i {
  font-size: 2rem;
  color: #ff0000;
}

.cert-item h4 {
  color: #2d3748;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.cert-item p {
  color: #718096;
  font-size: 0.9rem;
  margin: 0;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .summary-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .cert-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .summary-grid {
    grid-template-columns: 1fr;
  }

  .skill-category,
  .summary-item,
  .certifications {
    padding: 1.5rem;
  }

  .skill-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }

  .cert-item {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
} 